@media (max-width: 768px) {
    .level-0 {
        > :first-child { width: 100%; }
        > :last-child { width: 0%; }
    }
    
    .level-1 {
        > :first-child { width: 90%; }
        > :last-child { width: 10%; }
    }
    
    .level-2 {
        > :first-child { width: 10%; }
        > :last-child { width: 90%; }
    }
}